
.change .yd-btn-block{
    background: #f7f7f7;
    color: #333;
    margin-top: 0;
    height: auto;
}
.change .yd-datetime-input{
    padding: .3rem 0;
}
.change .yd-cell{
    position: inherit;
}
.change .yd-cell-box{
    margin-bottom: 0;
}
.change .yd-cell-item{
    background: #f7f7f7;
    padding: 0;
    height: 1.2rem;
}
.change .yd-cell-right{
    min-height: .4rem;
}
.change .yd-cell-right input[type="text"]{
    height: .4rem;
    max-height: .4rem;
}
.change .yd-cell-icon{
    display: none;
}
.change .yd-cell-left{
    font-size: .34rem;
}
.change .yd-cell-item .yd-datetime-input{
    font-size: .34rem;
    color: #93887F;
    margin-left: .5rem;
}
.change .yd-cell-arrow:after {
    margin: 0;
    content: "";
}



